body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
}

@font-face {
  font-family: "Press-Start-2P";
  font-style: normal;
  font-weight: 200;
  src: url(/pokemon-battler/static/media/PressStart2P-Regular.81c0aae9.ttf);
}

.App {
  text-align: center;
  font-family: "Press-Start-2P";
  background-color: #f8ee5f;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  height: 100vh;
  width: 100%;
  overflow: scroll;
  overflow-x: hidden;
}

/* ::-webkit-scrollbar {
  width: 0px;
  background: transparent;
} */

.title {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 80px;
  margin-top: 3px;
  margin-bottom: 3px;
}
.title > p {
  margin: 0;
  padding: 0;
  font-size: 3.3vw;
}
.pokeball {
  margin-top: 5px;
  margin-left: 15px;
  height: 75px;
  width: 75px;
}

.faded {
  -webkit-filter: brightness(10%);
          filter: brightness(10%);
}

.volume {
  position: absolute;
  margin-left: 70vw;
  margin-top: -15px;
  height: 30px;
  width: 30px;
}

.volume:hover {
  height: 35px;
  width: 35px;
}

.muted {
  opacity: 0.6;
}

.teamBuilder {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 100%;
}

.teamsContainer > div > div > ul {
  list-style-type: none;
}

.team1 {
  display: inline-flex;
  flex-direction: column;
  align-items: flex-start;
}
.battleStage {
  width: 100%;
}
.team2 {
  display: inline-flex;
  flex-direction: column;
  align-items: flex-end;
}

#BattleButton {
  margin: 5px;
}

.battleContainer {
  background-color: white;
  height: 570px;
  width: 650px;
  border: 2px dashed black;
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
}

.battleInputs {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 3px;
}

.sprite {
  height: 100%;
  width: 100%;
}

.side > p > img {
  height: 16px;
  width: 16px;
}

.side {
  padding: 5px;
}

.spriteContainer {
  height: 160px;
  width: 160px;
}

.player1Sprite {
  margin-left: 20px;
}

.player2Sprite {
  margin-left: 380px;
}

.options {
  margin: 3px;
}

.battleMessages {
  border-top: 2px dashed black;
  height: 20px;
  width: 106%;
  display: flex;
  flex-direction: column;
  justify-content: baseline;
  align-items: center;
}

.message {
  margin-top: 5px;
  font-size: 16px;
}
.playermessage {
  margin-top: 5px;
  font-size: 16px;
}

.fullhp {
  background-color: #28a745 !important;
}

.halfhp {
  background-color: #f3e001 !important;
}

.onefifthhp {
  background-color: #cc0101 !important;
}

.type {
  height: 25px;
  width: 70px;
}

/* status colors */
.poison {
  background-color: rgb(154, 39, 196);
}
.burn {
  background-color: rgb(221, 65, 15);
}
.paralyze {
  background-color: rgb(199, 162, 33);
}
.frozen {
  background-color: rgb(11, 79, 233);
}
.sleep {
  background-color: rgb(111, 104, 112);
}

/* hp displayed in number values */
.smallText {
  font-size: 14px;
}
.smallHP {
  text-align: left;
  margin-left: 35px;
  margin-bottom: 0;
}

.playerItems {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 170px;
  width: 100%;
  margin-top: 5px;
  margin-bottom: 5px;
}

.playerItem {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 170px;
  width: 170px;
  margin-top: 2px;
  margin-bottom: 2px;
}

.playerItem > img {
  height: 40px;
  width: 40px;
}

.playerItem:hover {
  box-shadow: 0 0 3px 2px;
  cursor: pointer;
}

.playerMoves {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  height: 170px;
  width: 100%;
  margin-top: 5px;
  margin-bottom: 5px;
}

.playerMoves > p {
  margin-bottom: 0;
}

.pokemonName {
  text-align: left;
  font-size: 16px;
  text-decoration: underline;
}

.pokemonMove {
  display: flex;
  flex-direction: row;
  align-items: center;
  text-align: left;
  justify-content: space-between;
  font-size: 16px;
  height: 55px;
  width: 100%;
}

.pokemonMove > .type {
  height: 25px;
  width: 70px;
}

.btn:hover {
  cursor: pointer;
}

.playerTeam {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 170px;
  width: 100%;
  margin-top: 5px;
  margin-bottom: 5px;
}

.playerTeamSprite {
  height: 170px;
  width: 170px;
}

.playerTeamSprite > img {
  height: 102px;
  width: 102px;
}

.playerTeamSprite:hover {
  box-shadow: 0 0 3px 2px;
  cursor: pointer;
}

.badgesContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  height: 600px;
  width: 100%;
}

.badgesContainer img {
  height: 45px;
  width: 45px;
}

.badgesContainer button {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  font-size: 16px;
  height: 60px;
  width: 100%;
  border: 1px solid black;
}

